import * as userApi from "../api/user";

export default {
  namespaced: true, //开启命名空间之后，使用mutations必须把模块名作为前缀才能使用，如：store.commit("loginUser/setLoading",true)
  state: {
    loading: false, //是否正在登录中
    user: null, //当前登录的用户
  },
  getters: {
    status(state) {
      if (state.loading) {
        return "loading";
        // 当前user有值就返回登录
      } else if (state.user) {
        return "login";
      } else {
        // 没有登陆
        return "unlogin";
      }
    }
  },
  mutations: {
    setLoading(state, payload) {
      state.loading = payload;
    },
    setUser(state, payload) {
      state.user = payload;
    },
  },
  // actions处理异步的
  actions: {
    // 登录方法
    async login(ctx, payload) {
      ctx.commit("setLoading", true);
      const resp = await userApi.login(payload.loginId, payload.loginPwd);
      ctx.commit("setUser", resp);
      ctx.commit("setLoading", false);
      // console.log(resp);
      return resp;
      
    },
    //检查是否已有登陆用户
    async whoAmI(ctx) {
      ctx.commit("setLoading", true);
      const resp = await userApi.whoAmI();
      ctx.commit("setUser", resp);
      ctx.commit("setLoading", false);
    },
    // 注销用户
    async loginOut(ctx) {
      ctx.commit("setLoading", true);
      await userApi.loginOut();
      ctx.commit("setUser", null);
      ctx.commit("setLoading", false);
    },
  },
};
